import { type Browser } from 'detect-browser';
import { browser_api } from '@app/browser_api';

export enum SessionStorageKey {
  IsProxyControlledByOtherExtensions = 'isProxyControlledByOtherExtensions',
  IsAdBlockersWork = 'isAdBlockersWork',
  PopupWasOpened = 'popupWasOpened',
  MetaApiData = 'metaApiData',
  IsNetworkError = 'isNetworkError',
  ConnectRetryFailed = 'connectRetryFailed',
  LastProxyHost = 'lastProxyHost',
  LastProxyPort = 'lastProxyPort',
  IsInitialized = 'isInitialized',
  ApiHttpErrors = 'apiHttpErrors',
  ApiHttpSuccess = 'apiHttpSuccess',
  RequestBlockedError = 'requestBlockedError',
  UpdateSubscriptionInfoPeriod = 'updateSubscriptionInfoPeriod',
  email = 'email',
  SessionData = 'sessionData',
}

export enum AvailableBanner {
  Naruzhu = 'naruzhu',
  Telegram = 'telegram',
  Ulta = 'ulta',
  YouboostPremium = 'youboost-premium',
  YouboostAndroid = 'youboost-android',
}

export enum PaymentMethod {
  Sbp = 'sbp',
  Card = 'card',
}

export type MetaApiData = {
  apiBaseUrl: string;
  cookieDomain: string;
  availableAlarmTries: number;
  showOnUpdateVersion?: string;
  currentVersion: string;
  requiredVersion: string;
  supportedBrowsers: Browser[];
  proxyUpdatePeriod?: number;
  proxyUpdatePeriodPremium?: number;
  shouldUpdatePAC?: string;
  shouldUpdatePACVersion?: string;
  availableBannersData?: string;
  androidAppUrl?: string;
  availablePaymentMethods?: PaymentMethod[];
  publicOfferUrl?: string;
  advMinVersion?: string;
  updateSubscriptionInfoPeriod?: number;
  adfoxBannerUrl?: string;
};

type ApiHttpErrors = Record<
  string,
  {
    timestampUtc: string;
    message: string;
    errorData: unknown;
    status: number;
    statusText: string;
    requestData: unknown;
  }
>;

type ApiHttpSuccess = Record<
  string,
  {
    timestampUtc: string;
    status: number;
    statusText: string;
    url: string;
  }
>;

export interface SessionDataType {
  timestamp: number;
  session_id: string;
}

type StorageData = {
  isProxyControlledByOtherExtensions?: boolean;
  isAdBlockersWork?: boolean;
  popupWasOpened?: boolean;
  metaApiData?: MetaApiData;
  isNetworkError?: boolean;
  connectRetryFailed?: boolean;
  lastProxyHost?: string;
  lastProxyPort?: number;
  isInitialized?: boolean;
  apiHttpErrors?: ApiHttpErrors;
  apiHttpSuccess?: ApiHttpSuccess;
  requestBlockedError?: string;
  updateSubscriptionInfoPeriod?: number;
  email?: string;
  sessionData?: SessionDataType;
};

export async function getSessionStorageData<Key extends SessionStorageKey>(
  keys?: Key | Key[] | null,
): Promise<Partial<Pick<StorageData, Key>>> {
  if (keys == null) {
    // null / undefined => вернуть всё
    return browser_api.storage.session.get() as Promise<StorageData>;
  }

  if (Array.isArray(keys) && keys.length === 0) {
    // Пустой массив => гарантированно {}
    return {} as Partial<Pick<StorageData, Key>>;
  }

  return browser_api.storage.session.get(keys) as Promise<
    Partial<Pick<StorageData, Key>>
  >;
}

export async function setSessionStorageData(
  data: Partial<StorageData>,
): Promise<void> {
  await browser_api.storage.session.set(data);
}

export async function setSessionProxyData(
  lastProxyHost: string,
  lastProxyPort: number,
): Promise<void> {
  await setSessionStorageData({ lastProxyHost, lastProxyPort });
}

export async function deleteSessionStorageData(
  keys: SessionStorageKey[] | SessionStorageKey,
): Promise<void> {
  await browser_api.storage.session.remove(keys);
}
